.preloader{
    height: 100vh;
    width: 100%;
    background: #081b29;
    color: #00abf0;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 33;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.preloader .texts-containe{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    width: 200px;
    font-size: 20px;
    font-weight: 200;
    overflow: hidden;
    color: #00abf0;
}