@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;600&display=swap');

body{
    background-color: aliceblue;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px;
    font-family:'Poppins',sans-serif;
}

.app{
    width: 400px;
    padding: 25px;
    color: #081b29;
    border-radius: 10px;
    box-shadow: 4px 4px 15px 5px #081b29;
    background:#00abf0;
}
.app h1{
    text-align: center;
    font-size: 24px;
    font-weight: 400;
}
.input-wrapper{
    display: flex;
}
.input-wrapper input{
    width: 100%;
    padding: 5px 10px;
    height: 30px;
    font-size: 14px;
    border: none;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.input-wrapper input:focus{
    outline: none;
}
.input-wrapper button{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    color: #fff;
    border: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    cursor: pointer;
    width: 80px;
}
.content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background: #00abf0 ; */
    background-color: #081b29;
    color: #00abf0;
    margin-top: 10px;
    border-radius: 10px;
}
.d-flex{
    display: flex;
    align-items: center;
    justify-content: center;
}
.flex-c{
    flex-direction: column;
}
.location{margin-top: 20px;}
.location h2{
    margin: 0;
    margin-left: 10px;
    font-weight: 300;
}
.location h2 span{
    font-size: 14px;
}
.datetext{
    font-size: 14px;
}
.weatherdesc h3{
    margin: 0;
    text-transform: capitalize;
    font-weight: 400;
}
.tempstats{
    width: 100%;
    padding: 20px 0px;
}
.tempstats h1{
    font-size: 64px;
    font-weight: 200;
    margin: 0;
}
.tempstats h1 span{
    font-size: 38px;
}
.tempstats h3{
    font-weight: 400;
}
.windstats{
    padding: 0px 20px;
    background:rgba(0,0,0,0.3);
    margin-bottom: 20px;
    border-radius: 10px;
}
.windstats h3{
    margin-left: 10px;
    font-weight: 400;
}
.humidity{
    padding: 0px 20px;
    background:rgba(0,0,0,0.3);
    margin-bottom: 20px;
    border-radius: 10px;
}
.humidity h3{
    margin-left: 20px;
    font-weight: 400;
}
.precipitation{
    padding: 0px 20px;
    background:rgba(0,0,0,0.3);
    margin-bottom: 20px;
    border-radius: 10px;
}
.precipitation h3{
    margin-left: 10px;
    font-weight: 400;
}
@media only screen and (max-width:400px) {
    .input-wrapper{
        padding-left: 10px;
        padding-right: 10px;
    }
    .content{
        margin-left: 10px;
        margin-right: 10px; 
        margin-top: 20px;
        margin-bottom: 20px; 
    }
    .heading{
         margin-left: 0px;
        margin-right: 10px;
    }
    .app{
        margin-left: 100px;
        width: 100%;
        height: 100%;
        margin: 0;
        align-items: center;
        padding: 0;
        overflow: hidden;
        margin-top: 50px;
        margin-right:100px;

    }
    .harsh{
        position: absolute;
        margin-top: -100px;
        left: 20px;
    }
    .windstats{
        width: 180px;
    }
}